___CSS_LOADER_EXPORT___.push([module.id, ".v-autocomplete{font-size:15px}", "",{"version":3,"sources":["webpack://./resources/js/components/autocomplete/SelectOficina.vue"],"names":[],"mappings":"AAuHA,gBACA,cACA","sourcesContent":["<template>\r\n    <div>\r\n        <v-autocomplete\r\n        v-model=\"oficina_selected\"\r\n        clearable\r\n        class=\"mt-0 pt-0\"\r\n        dense\r\n        label=\"Oficina\"\r\n        outlined\r\n        :items=\"oficinas_res\"\r\n        :filter=\"customFilterOficina\"\r\n        item-value=\"iduoper\"\r\n        item-text=\"nombre\"\r\n        :search-input.sync=\"oficinas_search\"\r\n        required\r\n        :disabled=\"disabled\"\r\n\r\n        :rules=\"rules\"\r\n    >\r\n        <template v-slot:selection=\"data\">\r\n            <small>\r\n                <strong>{{ data.item.dependencia }}</strong>\r\n                {{ data.item.nombre }}</small\r\n            >\r\n        </template>\r\n\r\n        <template v-slot:no-data>\r\n            <v-list-item>\r\n                <v-list-item-title>\r\n                    <template v-if=\"oficinas_search?.length > 2\">\r\n                        Datos no encontrados para\r\n                        <strong>\r\n                            {{ oficinas_search }}\r\n                        </strong>\r\n                    </template>\r\n                    <template v-else>\r\n                        Digite más de\r\n                        <strong> 2</strong> caracteres.\r\n                    </template>\r\n                </v-list-item-title>\r\n            </v-list-item>\r\n        </template>\r\n\r\n        <template v-slot:item=\"data\">\r\n            <v-list-item-content>\r\n                <v-list-item-title v-html=\"data.item.dependencia\">\r\n                </v-list-item-title>\r\n                <v-list-item-subtitle>\r\n                    {{ data.item.nombre }}\r\n                </v-list-item-subtitle>\r\n            </v-list-item-content>\r\n        </template>\r\n    </v-autocomplete>\r\n    </div>\r\n  \r\n</template>\r\n<script>\r\nexport default {\r\n    name: \"SelectOficina\",\r\n    props: {\r\n        value: String,\r\n\r\n        disabled: {\r\n            default: false,\r\n            type: Boolean,\r\n        },\r\n        user: Number,\r\n        rules: Array,\r\n    },\r\n    data: () => ({\r\n        oficinas_res: [],\r\n        oficinas_search: \"\",\r\n        data: [],\r\n    }),\r\n\r\n    methods: {\r\n        customFilterOficina(item, queryText, itemText) {\r\n            const nombre = item.nombre.toLowerCase();\r\n            const dependencia = item.dependencia.toLowerCase();\r\n            const searchText = queryText.toLowerCase();\r\n            return (\r\n                nombre.indexOf(searchText) > -1 ||\r\n                dependencia.indexOf(searchText) > -1\r\n            );\r\n        },\r\n        async BuscarOficinas(term) {\r\n            let user_filter = this.user ? \"/\" + this.user : \"\";\r\n            let res = await axios.get(\r\n                \"/get-data/oficinas/\" + term + user_filter\r\n            );\r\n            return res.data.datos;\r\n        },\r\n    },\r\n    watch: {\r\n        async oficinas_search(val) {\r\n            if (!val) return;\r\n            if (val.length < 2) return;\r\n            let res = await this.BuscarOficinas(val);\r\n            this.oficinas_res = res;\r\n        },\r\n        async oficina_selected(val) {\r\n            let res = await this.BuscarOficinas(val);\r\n            this.oficinas_res = res;\r\n        },\r\n    },\r\n    computed: {\r\n        oficina_selected: {\r\n            get() {\r\n                return this.value;\r\n            },\r\n            set(newValue) {\r\n                this.$emit(\"input\", newValue);\r\n            },\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style>\r\n.v-autocomplete {\r\n    font-size: 15px;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
