___CSS_LOADER_EXPORT___.push([module.id, ".tr-selected{background-color:rgba(1,48,90,.2);border:1px dashed #444;color:#01305a;font-weight:600}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Inventario/Components/BusquedaAvanzadaComponent.vue"],"names":[],"mappings":"AAoQA,aACA,iCAAA,CACA,sBAAA,CACA,aAAA,CACA,eACA","sourcesContent":["<template>\r\n    <v-dialog v-model=\"dialog\" fullscreen scrollable>\r\n        <template v-slot:activator=\"{ on, attrs }\">\r\n            <v-btn class=\"mx-sm-2 order-sm-3\" dark color=\"primary\" v-bind=\"attrs\" v-on=\"on\">\r\n                <v-icon>mdi-magnify-expand</v-icon>\r\n            </v-btn>\r\n        </template>\r\n\r\n        <v-card tile>\r\n            <v-toolbar>\r\n                <v-app-bar-nav-icon @click=\"dialog = false\">\r\n                    <v-icon>mdi-arrow-left</v-icon>\r\n                </v-app-bar-nav-icon>\r\n\r\n                <v-toolbar-title>Busqueda Avanzada</v-toolbar-title>\r\n            </v-toolbar>\r\n\r\n            <v-card-text style=\"height: 90vh\">\r\n                <v-row class=\"mt-3\">\r\n                    <v-col cols=\"12\" class=\"pb-1 pt-0\">\r\n\r\n                        <SelectOficina v-model=\"oficina_selected\" />\r\n                    </v-col>\r\n                </v-row>\r\n\r\n                <v-card :loading=\"loading_table\" :disabled=\"!oficina_selected\">\r\n                    <v-overlay absolute :value=\"loading_table\">\r\n                        <v-progress-circular indeterminate size=\"64\"></v-progress-circular>\r\n                    </v-overlay>\r\n                    <v-card-title>\r\n                        <v-row>\r\n                            <v-col cols=\"6\" sm=\"7\" class=\"pb-1\">\r\n                                <v-combobox v-model=\"mostrar_selected\" :items=\"items_combobox\" label=\"Mostrar\" outlined\r\n                                    hide-details dense></v-combobox>\r\n                            </v-col>\r\n\r\n                            <v-col cols=\"6\" sm=\"5\" class=\"pb-1\">\r\n                                <v-text-field v-model=\"corr_search\" min=\"1\" label=\"Stiker 2019\" dense outlined\r\n                                    hide-details :prefix=\"\r\n                                    oficina_selected ? oficina_selected.split('.')[0] + ' - ' : ''\"\r\n                                    type=\"number\"></v-text-field>\r\n                            </v-col>\r\n\r\n                            <v-col cols=\"12\" class=\"pb-1\">\r\n                                <v-text-field v-model=\"area_search\" append-icon=\"mdi-magnify\" label=\"Buscar\"\r\n                                    hide-details outlined dense></v-text-field>\r\n                            </v-col>\r\n                        </v-row>\r\n                    </v-card-title>\r\n                    <v-simple-table>\r\n                        <template v-slot:default>\r\n                            <thead class=\"grey lighten-3\">\r\n                                <tr>\r\n                                    <th class=\"text-left\">Estado</th>\r\n                                    <th class=\"text-left\">Stiker 2019</th>\r\n                                    <th class=\"text-left\">Codigo</th>\r\n                                    <th class=\"text-left\">Descripcion</th>\r\n                                </tr>\r\n                            </thead>\r\n                            <tbody>\r\n                                <tr v-for=\"(item, index) in bienes_result\" :key=\"index\" :class=\"[\r\n                                    index == tr_index ? 'tr-selected' : '',\r\n                                ]\" @click=\"onSelectColum(item, index)\" @dblclick=\"onSelectColumDobleClik(item)\">\r\n                                    <td>\r\n                                        <v-list-item-avatar size=\"20\" :color=\"\r\n                                            item.registrado\r\n                                                ? 'green'\r\n                                                : 'grey'\r\n                                        \">\r\n                                            <v-icon small dark>\r\n                                                mdi-checkbox-marked-circle\r\n                                            </v-icon>\r\n                                        </v-list-item-avatar>\r\n                                    </td>\r\n                                    <td>\r\n                                        <small>{{ item.cod_ubicacion | correlativo }}</small>\r\n                                    </td>\r\n                                    <td>{{ item.codigo }}</td>\r\n                                    <td>{{ item.descripcion }}</td>\r\n\r\n                                </tr>\r\n                            </tbody>\r\n                        </template>\r\n                    </v-simple-table>\r\n\r\n                    <v-card-actions>\r\n                        <v-spacer></v-spacer>\r\n                        <v-pagination v-model=\"page\" class=\"\" :total-visible=\"5\" :length=\"pages\"></v-pagination>\r\n                        <v-spacer></v-spacer>\r\n                    </v-card-actions>\r\n                </v-card>\r\n            </v-card-text>\r\n            <v-divider></v-divider>\r\n\r\n            <v-card-actions>\r\n                <h5>Total: {{ total_result }}</h5>\r\n                <v-spacer></v-spacer>\r\n            </v-card-actions>\r\n        </v-card>\r\n    </v-dialog>\r\n</template>\r\n<script>\r\nimport axios from \"axios\";\r\nimport SelectOficina from \"../../../components/autocomplete/SelectOficina.vue\";\r\n\r\nexport default {\r\n    props: {\r\n        oficinas: Array,\r\n    },\r\n    data: () => ({\r\n        loading_table: false,\r\n        tr_index: null,\r\n        tr_item: {},\r\n        items_combobox: [\"Todos\", \"Registrados\", \"Sin registrar\"],\r\n        dialog: false,\r\n        area_selected: null,\r\n        search: \"\",\r\n        bienes_result: [],\r\n        mostrar_selected: \"Todos\",\r\n        area_search: \"\",\r\n        corr_search: null,\r\n        page: 1,\r\n        total_result: 0,\r\n        pages: 1,\r\n        oficina_selected: null,\r\n        oficinas_res: [],\r\n        oficinas_search: \"\",\r\n        area_selected: null,\r\n        areas_by_oficna: [],\r\n    }),\r\n    filters: {\r\n        correlativo: function (value) {\r\n            if (!value) return '-'\r\n            let corr_area = value.split(/[-.]+/);\r\n            //value = value.toString()\r\n            return corr_area[0] + ' - ' + corr_area.slice(-1);\r\n        }\r\n    },\r\n    methods: {\r\n        async Editar(id) {\r\n            let res = await axios.get(\"/inventario/get-inventario/\" + id);\r\n        },\r\n        onSelectColum(item, index) {\r\n            this.tr_index = index;\r\n            this.tr_item = item;\r\n        },\r\n        onSelectColumDobleClik(item) {\r\n            item.registrado = item.registrado == 1 ? true : false;\r\n            this.$emit(\"setData\", item);\r\n            this.dialog = false;\r\n            this.resetAll();\r\n        },\r\n        SeleccionarBien() {\r\n            tr_item.registrado = tr_item.registrado == 1 ? true : false;\r\n            this.$emit(\"setData\", this.tr_item);\r\n            this.dialog = false;\r\n        },\r\n\r\n        async getBienes() {\r\n            let res = await axios.post(\"/inventario/get-bienes?page=\" + this.page, {\r\n                area: this.oficina_selected,\r\n                mostrar: this.mostrar_selected,\r\n                correlativo: this.corr_search,\r\n                term: this.area_search,\r\n            });\r\n\r\n            this.page = res.data.datos.current_page;\r\n            this.total_result = res.data.datos.total;\r\n            this.pages = res.data.datos.last_page;\r\n\r\n            return res.data.datos.data;\r\n        },\r\n      \r\n        customFilterOficina(item, queryText, itemText) {\r\n            const nombre = item.nombre.toLowerCase();\r\n            const codigo = item.codigo.toLowerCase();\r\n            const searchText = queryText.toLowerCase();\r\n            return (nombre.indexOf(searchText) > -1 ||\r\n                codigo.indexOf(searchText) > -1);\r\n        },\r\n        async BuscarOficinas(term) {\r\n            let res = await axios.get(\"/inventario/search-oficinas/\" + term);\r\n            return res.data.datos;\r\n        },\r\n        resetAll() {\r\n            this.bienes_result = [];\r\n            this.area_selected = null;\r\n            this.page = 1;\r\n            this.total_result = 0;\r\n            this.pages = 1;\r\n            this.mostrar_selected = \"Todos\";\r\n        },\r\n    },\r\n    watch: {\r\n        async area_selected(val) {\r\n            if (!val)\r\n                return;\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n            let res = await this.getBienes();\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n\r\n        async corr_search(val) {//correlativo\r\n            //if (!val) return;\r\n            //if (val < 1) return;\r\n\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n            let res = await this.getBienes();\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n        async oficinas_search(val) {\r\n            if (!val)\r\n                return;\r\n            if (val.length < 2)\r\n                return;\r\n            let res = await this.BuscarOficinas(val);\r\n            this.oficinas_res = res;\r\n        },\r\n        async oficina_selected(val) {\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n\r\n            let res = await this.getBienes();\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n        async area_search(val) {\r\n            //if (!val) return;\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n            let res = await this.getBienes();\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n        async page(val, old_val) {\r\n            if (val == old_val)\r\n                return;\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n            let res = await this.getBienes();\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n        async mostrar_selected(val) {\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n            let res = await this.getBienes();\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n    },\r\n    components: { SelectOficina }\r\n};\r\n</script>\r\n\r\n<style>\r\n.tr-selected {\r\n    background-color: rgba(1, 48, 90, 0.2);\r\n    border: 1px dashed #444;\r\n    color: #01305a;\r\n    font-weight: 600;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
